/*-----scrollDiv-for back to the top of the page ------*/
.scroll-div {
    position: fixed; /* 固定定位 */
    bottom: 30px; /* 距离页面底部 300px */
    right: 20px; /* 距离页面右边 20px */
    width: 80px; /* div 的宽度 */
    height: 80px; /* div 的高度 */
    background-image:  url('../../public/images/back-scroll-icon.png'); /* 背景图片路径 */
    background-size: cover; /* 确保背景图片覆盖整个 div */
    background-position: center; /* 背景居中 */
    border-radius: 50%; /* 圆形外观 */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3); /* 添加阴影 */
    cursor: pointer; /* 鼠标变为手型 */
    opacity: 0.8; /* 设置透明度 */
    transition: opacity 0.3s ease, transform 0.3s ease; /* 平滑动画效果 */
}

.scroll-div:hover {
    opacity: 1; /* 鼠标悬浮时完全不透明 */
    transform: scale(1.1); /* 鼠标悬浮时轻微放大 */
}

