
.page-container {
    display: flex;
    padding: 20px;
    gap: 20px;
}
.price-info {
    display: flex; /* 启用 Flexbox 布局 */
    justify-content: space-between; /* 子元素左右对齐 */
    align-items: center; /* 子元素垂直居中（可选） */
    width: 100%; /* 确保占满父容器 */
}
.product-title {
    text-align: left; /* 将文字内容靠左显示 */
    margin: 0; /* 移除默认段落的上、下边距 */
    font-size: 16px; /* 调整字体大小（可选） */
    font-weight: bold; /* 加粗字体（可选） */
    color: #333; /* 设置文字颜色 */
}
/* 左侧筛选框 */
.filter-options {
    width: 25%;
    background-color: #f9f9f9;
    padding: 20px;
    border: 1px solid #ccc;
}
.radio-label {
    display: flex; /* 使用 Flexbox 布局 */
    align-items: center; /* 垂直方向居中对齐 */
    text-align: left; /* 将文字靠左 */
    font-size: 16px; /* 调整文字大小 */
    gap: 8px; /* 设置圆圈和文字之间的间距 */
}

.radio-input {
    width: 20px; /* 调整圆圈宽度 */
    height: 20px; /* 调整圆圈高度 */
    accent-color: #4CAF50; /* 设置圆圈的颜色（绿色） */
    cursor: pointer; /* 鼠标悬浮时显示手型 */
}
.filter-options h3 {
    margin-bottom: 20px;
}

.filter-section {
    margin-bottom: 15px;
}

.filter-section p {
    margin: 0 0 10px 0;
    font-weight: bold;
}

.filter-section label {
    display: block;
    margin-bottom: 5px;
}
.sizeChart{
    padding: 10px;
    color: black;
}
.size-chart-button {
    width: 100%;
    padding: 10px;
    background-color: white;
    border: 1px solid black;
    cursor: pointer;
}

/* 右侧产品展示 */
.product-options {
    width: 75%;
}

.product-options h2 {
    margin-bottom: 20px;
}

.tabs {
    display: flex;
    gap: 10px;
    margin-bottom: 20px;
}
.labelStyle{
    color: black;
}

.tabs button {
    padding: 10px 20px;
    background-color: white;
    border: 1px solid #ccc;
    cursor: pointer;
}

.product-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
}

.product-card {
    border: 1px solid #ccc;
    padding: 10px;
    text-align: center;
    background-color: #fff;
}

.product-card img {
    max-width: 100%;
    height: auto;
    margin-bottom: 10px;
}

.product-card p {
    margin: 5px 0;
}

.order-now {
    padding: 10px 20px;
    background-color: #4CAF50;
    color: white;
    border: none;
    width: 100%;
    cursor: pointer;
}
.tShirtBackGroundDiv {
    margin-top: -90px;
    position: relative;
    width: 100%;
    height: 40vh;
    overflow: hidden;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url('../../public/images/Dual Side T-shirt Artwork Mockup - Tiaki Taonga Trust - WHITE.jpg');
    justify-content: center;
}
.tShirtDivRight{
    flex:2;
    position: relative;
    width: 100%;
    height: 40vh;
    overflow: hidden;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url('../../public/images/Dual Side T-shirt Artwork Mockup - Tiaki Taonga Trust - WHITE.jpg');
    justify-content: center;
}
.tShirtDivRight2{
    flex:2;
    position: relative;
    width: 100%;
    height: 40vh;
    overflow: hidden;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url('../../public/images/Dual Side T-shirt Artwork Mockup.png');
    justify-content: center;
}
.tShirt-text-container{
    position: relative;
    margin: 120px 50px 20px 50px;
    font-size: 66px;
    color: white;
    z-index: 20;
}
.about-label-container{
    position: relative;
    margin: 80px 50px 20px 50px;
    color:white;
    z-index: 20;
}

.size-chart-container {
    max-width: 1200px;
    margin: 0 auto;
}

.back-link {
    display: inline-block;
    margin-bottom: 20px;
    text-decoration: none;
    color: #007BFF;
    font-size: 16px;
}

h2 {
    margin-top: 40px;
    font-size: 24px;
    font-weight: bold;
}

.size-chart {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    margin-top: 20px;
}

.size-chart-column {
    width: 48%;
}

.size-row {
    display: flex;
    justify-content: space-between;
    padding: 8px 0;
    border-bottom: 1px solid #ddd;
}

.size-row.header {
    font-weight: bold;
    border-bottom: 2px solid #333;
}

.size-row span {
    width: 50%; /* 每列占 50% */
    text-align: left;
}

.note {
    font-size: 12px;
    color: #555;
    margin-top: 10px;
}
