/* page 1 */
.legal-status-container {
    margin: 0 auto;
    padding: 20px;
    font-family: Arial, sans-serif;
    color: #333;
}

/* Title styles */
.legal-title {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 10px;
}

.legal-description {
    font-size: 16px;
    line-height: 1.5;
    margin-bottom: 20px;
}

/* Image styles */
.image-container {
    position: relative;
    width: 100vh;
    height: 60vh;
    overflow: hidden;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url('../../public/images/legalstatus.png');
    display: flex;
    flex-direction: column; /* 垂直排列 */
    justify-content: center; /* 垂直方向居中 */
    align-items: center; /* 水平方向居中 */
    text-align: center; /* 文本居中 */
    margin: 0; /* 移除外部间距 */
    padding: 0; /* 移除内部间距 */
    line-height: 1; /* 消除默认行高的间距 */
    gap: 0; /* Flex 子元素之间的间距设为 0 */
}

/* Subtitle and text styles */
.ko-matou-title {
    font-size: 20px;
    font-weight: bold;
    margin-top: 20px;
    margin-bottom: 10px;
}

.ko-matou-description {
    font-size: 16px;
    line-height: 1.5;
}
/*page 2*/

/* Main Container */
.succession-container {
    max-width: 900px;
    margin: 0 auto;
    padding: 20px;
    font-family: Arial, sans-serif;
    color: #333;
}

/* Title Section */
.succession-title {
    font-size: 28px;
    font-weight: bold;
    margin-bottom: 10px;
}

.succession-subtitle {
    font-size: 18px;
    color: #555;
    margin-bottom: 20px;
}

/* Content Wrapper */
.content-wrapper {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
}

/* Left Text Section */
.text-section {
    flex: 1;
    min-width: 300px;
}

.text-heading {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 10px;
}

.text-body {
    font-size: 16px;
    line-height: 1.6;
    margin-bottom: 20px;
}

.divider {
    border: none;
    border-top: 1px solid #ccc;
    margin: 20px 0;
}

.quote {
    font-style: italic;
    font-size: 16px;
    margin-bottom: 10px;
}

.quote-translation {
    font-size: 14px;
    color: #555;
    margin-bottom: 20px;
}

/* Right Image Section */
.image-section {
    flex: 1;
    position: relative;
    width: auto;
    height: auto;
    overflow: hidden;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url('../../public/images/KoAtou .png');
    justify-content: center; /* 垂直方向居中 */
    align-items: center; /* 水平方向居中 */
    text-align: center; /* 文本居中 */
    margin: 0; /* 移除外部间距 */
    padding: 0; /* 移除内部间距 */
    line-height: 1; /* 消除默认行高的间距 */
    gap: 0; /* Flex 子元素之间的间距设为 0 */
}








/*-----scrollDiv-for back to the top of the page ------*/
.scroll-div {
    position: fixed; /* 固定定位 */
    bottom: 30px; /* 距离页面底部 300px */
    right: 20px; /* 距离页面右边 20px */
    width: 80px; /* div 的宽度 */
    height: 80px; /* div 的高度 */
    background-image:  url('../../public/images/back-scroll-icon.png'); /* 背景图片路径 */
    background-size: cover; /* 确保背景图片覆盖整个 div */
    background-position: center; /* 背景居中 */
    border-radius: 50%; /* 圆形外观 */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3); /* 添加阴影 */
    cursor: pointer; /* 鼠标变为手型 */
    opacity: 0.8; /* 设置透明度 */
    transition: opacity 0.3s ease, transform 0.3s ease; /* 平滑动画效果 */
}

.scroll-div:hover {
    opacity: 1; /* 鼠标悬浮时完全不透明 */
    transform: scale(1.1); /* 鼠标悬浮时轻微放大 */
}

